import Vue from 'vue'
// directives
// 

// v-tip
//  `v-tip`()
import tip from './tip'
Vue.directive(tip.name, tip.func) // v-tip

// v-modal
//  `v-modal`(  v-model )
import modal from './modal'
Vue.directive(modal.name, modal.func) // v-modal

// v-coll
// 
import coll from './coll'
Vue.directive(coll.name, coll.func) // v-coll

// v-canvas
// 
import canvas from './canvas'
Vue.directive(canvas.name, canvas.func) // v-canvas

// v-lazy
//  /
// 
import lazy from './lazy'
Vue.directive(lazy.name, lazy.func) // v-lazy

export default {}
